<template>
  <div>
    <div  v-if="percentage" class="loading-bar">
      <div class="percentage" :style="{ width: percentage + '%' }">{{ percent }}%</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Bar',
  data() {
    return {
      percentage: 0,
    }
  },
  props: {
    niveau: {
      type: [Number, String],
    },
  },
  computed: {
    percent() {
      return this.percentage.toFixed()
    },
  },
  created() {
    var intval = setInterval(() => {
      if (this.percentage < this.niveau) this.percentage += 0.5
      else clearInterval(intval)
    }, 10)
  },
}
</script>
