<template>
  <footer class="footer container center margin-top">
    <span v-if="footer">
      <i :class="footer.icon">&nbsp;</i>
       {{ footer.name }} {{ getDateYear }}
    </span>
    <span v-else>
      <FooterLoading />
    </span>
  </footer>
</template>

<script>
import FooterLoading from '@/components/FooterLoading.vue'

export default {
  name: 'Footer',
  props: {
    footer: Object,
  },
  components: {
    FooterLoading,
  },
  computed: {
    getDateYear() {
      return new Date().toJSON().slice(0,4).replace(/-/g,'/');
    }
  },
}
</script>

