<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import { user } from '@/services'

export default {
  name: 'app',
  watch: {
    $route() {
      // se lance à chaque changement de URL
      user.removeUser()
    },
  },
  beforeUnmount() {
    user.removeUser()
  },
}
</script>