<template>
  <div v-if="data" :id="data.titre">
    <h2 class="text-grey padding-16">
      <i class="fas margin-right xxlarge text-teal" :class="data.icon"></i>
      {{ $filters.capitalize(data.titre) }}
    </h2>
    <div v-for="(item, index) in data.list" :key="index" class="container">
      <h5 class="opacity">
        <b>{{ item.titre }}</b>
          <span v-if="data.titre !== 'expériences professionnelles'"
            @click="scrollTop"
            class="fas fa-angle-double-up top">
            </span>
      </h5>
      <h6 class="text-teal">
        <i class="fas fa-calendar-alt margin-right"></i>
        {{ item.dateDebut }}
        <span v-if="item.dateFin"
          :class="[item.dateFin === 'Présent' ? 'tag teal round' : '']"
        > - {{ item.dateFin }}</span>
        <span v-if="item.duree"> ( {{ item.duree }} )</span>
      </h6>
      <p v-for="(ligne, index1) in item.lignes" :key="index1" v-html="ligne" @click="scrollToMe"></p>
      <p v-for="(ref, index2) in item.references" :key="index2" @click="toLink(ref)" :id="'id_ref'+index2">Référence</p>
      <hr v-if="index + 1 < data.list.length" />
    </div>
  </div>
  <div v-else>
    <Loading />
  </div>
</template>

<script>
import Loading from '@/components/Loading.vue'

export default {
  name: 'RightData',
  components: {
    Loading,
  },
  props: {
    data: Object,
  },
  methods: {
    scrollTop () {
      window.scrollTo({top: 0, behavior: 'smooth'});
      this.$router.push({name: 'home'});
    },
    scrollToMe(event){
      const idEvent = event.target.id;
      if (idEvent.substring(0, 3) == "id_") {
        this.scroll(idEvent.substring(3));
      }
    },
    scroll(id) {
      document.getElementById(id).scrollIntoView({
        behavior: "smooth"
      });
    },
    toLink(url){
      window.open(url, '_blank');
    }
  }
}

</script>
