<template>
  <div id="contact" class="modal" v-if="contactinfo">
    <div class="modal-content animate-zoom">
      <div class="container hautmodal">
        <i
          @mouseover="hover = 'far fa-times-circle'"
          @mouseleave="hover = 'fas fa-times-circle'"
          class="buttonX"
          :class="hover"
          @click="closeContact"
        ></i>
        <h1>{{ contactinfo.titre }}</h1>
      </div>
      <div class="container">
        <p>{{ contactinfo.invitation }}</p>
          <form @submit="envoiEmail">
          <div v-if="errors.length">
            <b>{{ contactinfo.notification }} :</b>
            <ul>
              <li v-for="(error, index) in errors" :key="index" class="errormessage">{{ error }}</li>
            </ul>
          </div>
          <p>
            <input
              class="input padding-16 border"
              type="text"
              required
              :placeholder="`${contactinfo.name}*`"
              name="name"
              v-model.trim="form.name"
              @keypress.enter.prevent="faireRien"
            />
          </p>
          <p>
            <input
              class="input padding-16 border"
              type="email"
              required
              :placeholder="`${contactinfo.email}*`"
              name="_replyto"
              v-model.trim="form.email"
              @keypress.enter.prevent="faireRien"
            />
          </p>
          <p>
            <input
              class="input padding-16 border"
              type="text"
              required
              :placeholder="`${contactinfo.subject}*`"
              name="subject"
              v-model.trim="form.subject"
              @keypress.enter.prevent="faireRien"
            />
          </p>
          <p>
            <textarea
              class="padding-16 border"
              required
              :placeholder="`${contactinfo.message}*`"
              name="message"
              v-model.trim="form.message"
            ></textarea>
          </p>
          <p>
            <button class="button" type="submit">{{ contactinfo.btvalider }}</button>
          </p>
        </form>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Contact',
  props: ['contactinfo'],
  data() {
    return {
      errors: [],
      form: {
        name: null,
        email: null,
        subject: null,
        message: null,
      },
      hover: 'fas fa-times-circle',
    }
  },
  methods: {
    faireRien() {},
    closeContact() {
      document.querySelector('#contact').style.display = 'none';
    },
    validEmail(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return re.test(email)
    },
    envoiEmail(event) {
      event.preventDefault();
      const { name, email, subject, message } = this.form;
      const {notname, notemail1, notemail2, notsubject, notmessage } = this.contactinfo;
      this.errors = [];
      if (!name) {
        this.errors.push(notname);
      }
      if (!email) {
        this.errors.push(notemail1);
      } else if (!this.validEmail(email)) {
        this.errors.push(notemail2);
      }
      if (!subject) {
        this.errors.push(notsubject);
      }
      if (!message) {
        this.errors.push(notmessage);
      }
      if (!this.errors.length) {
        this.submitForm();
      }
    },
    submitForm(){
      var myHeaders = new Headers();
      myHeaders.append('Content-Type', 'application/json');
      var myInit = {
        method: "POST",
        headers: myHeaders,
        mode: 'cors',
        cache: 'default',
        body: JSON.stringify(this.form)
      };
      var myRequest = new Request(process.env.VUE_APP_FORMSPREE, myInit);
      fetch(myRequest).then(() => {
        this.$router.go(-1);
        for (var item in this.form) {
          this.form[item] = null;
        }
        this.closeContact();
        alert('Votre message a bien été envoyé');
      })
      .catch((error) => {
        console.log(error);
      }).finally(() => {
      });
    },
  },
}
</script>
